<template>
  <v-container v-if="units">
    <section class="selection" v-if="!selectedUnit">
      <h1 class="mb-4">Please select Unit</h1>
      <v-btn class="mr-4" :key="`unit-${unit.key}`" v-for="unit in units" @click="onClickUnit(unit)">
        {{unit.key}}
      </v-btn>
    </section>
    <section class="play" v-if="selectedUnit">
      <audio
        ref="audio"
        preload
      >
        <source ref="audioSource" src="" />
      </audio>
      <div class="audio-item" :key="`item-${idx}`" v-for="(item, idx) in selectedUnit.list" :class="{active: lastPlayItem === item}">
        <v-btn class="ma-4" @click="onClickAudio(item)">
          {{item.path}}
        </v-btn>
        <!-- {{item.text}} -->
      </div>
    </section>
  </v-container>
</template>

<script>
  import axios from 'axios'
  import _ from 'lodash'

  export default {
    name: 'TestPage',

    data() {
      return {
        units: [],
        selectedUnit: null,
        lastPlayItem: null
      }
    },
    async mounted() {
      const response = await axios.get('./dictation.json')
      _.each(response.data, (list, key) => {
        this.units.push({
          key,
          list
        })
      })
    },
    methods: {
      onClickUnit(unit) {
        this.selectedUnit = unit
      },
      onClickAudio(item) {
        const { audio, audioSource } = this.$refs
        audio.src = `${window.location.origin}/mp3/${item.path}`
        audioSource.src = `${window.location.origin}/mp3/${item.path}`
        audioSource.type = 'audio/mpeg'
        audio.play()
        this.lastPlayItem = item
      }
    }
  }
</script>

<style lang="scss" scoped>
.audio-item.active {

  background-color: #ffdddd;

}
</style>
